

//VARIABLES
@import "base/variables";

//MIXINS
@import "base/mixins";


//RESET
@import "base/reset";

//STANDART SETUP
@import "base/standart";

//MEDIA QUERIES
@import "base/media";

//  PERFECT-SCROLLBAR CSS
@import 'app/perfect-scrollbar.css';



@include reset-2();

// HEADER BLOCK
.wrapper {
    display: flex;
    min-height: 600px;
    height: 100vh;
 }    // min-width: 1024px

.navigation {
    flex-basis: 8.15%;
    // min-height: 600px
    background-color: $headert-bg-color;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column; }

.avatar {
    width: 5.9rem;
    height: 5.9rem;
    position: relative;
    margin-top: 2.1rem;
    margin-bottom: 1.5rem;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: flex-start;

    &__img {
        width: 100%; }

    &__mails {
        position: absolute;
        top: -2px;
        right: -2px;
        background-color: #ff503f;
        border-radius: 50%;
        width: 2.1rem;
        height: 2.1rem;
        text-align: center;
        color: $white-color; } }

.dots {
    width: 1.7rem;
    height: 1.7rem;
    margin-bottom: .5rem;

    &__img {
        width: 100%;
        height: 100%;
        fill: $non-active-color; } }

.board {
    width: 100%;
    height: 7.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;

    &_active {
        box-shadow: .3rem 0 0 $accent inset; }

    &__img {
        width: 2.9rem;
        fill: $accent; }

    &__name {
        font-size: 1.2rem;
        line-height: 2.5rem;
        text-shadow: 0 1px 1px rgba(0, 0, 0, 0.35);
        color: $white-color; } }

.projects {
    width: 100%;
    height: 7.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;

    &__icon {
        position: relative;
        height: 2.9rem; }

    &__img {
        width: 2.9rem;
        fill: $non-active-color; }


    &__name {
        font-size: 1.2rem;
        line-height: 2.5rem;
        // letter-spacing: -0.3px
        text-shadow: $text-shadow;
        color: $white-color; }

    &__number {
        position: absolute;
        top: -50%;
        right: -50%;
        background-color: $accent;
        border-radius: 50%;
        width: 2.1rem;
        height: 2.1rem;
        text-align: center;
        color: $white-color; } }

.settings {
    width: 100%;
    height: 7.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;

    &__img {
        width: 2.9rem;
        fill: $non-active-color; }

    &__name {
        font-size: 1.2rem;
        line-height: 2.5rem;
        // letter-spacing: -0.3px
        text-shadow: $text-shadow;
        color: $white-color; } }

.upgrade {
    width: 100%;
    height: 7.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: auto;
    cursor: pointer;

    &__icon {
        position: relative;
        height: 2.9rem; }

    &__img {
        width: 2.9rem;
        fill: $non-active-color; }

    &__name {
        font-size: 1.2rem;
        line-height: 2.5rem;
        // letter-spacing: -0.3px
        text-shadow: $text-shadow;
        color: $white-color; }

    &__number {
        position: absolute;
        top: -50%;
        right: -50%;
        background-color: $accent;
        border-radius: 50%;
        width: 2.1rem;
        height: 2.1rem;
        text-align: center;
        color: $white-color; } }

.logout {
    width: 100%;
    height: 7.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: .4rem;
    cursor: pointer;

    &__img {
        width: 2.9rem;
        height: 2.9rem;
        fill: $non-active-color; }

    &__name {
        font-size: 1.2rem;
        line-height: 2.5rem;
        // letter-spacing: -0.3px
        text-shadow: $text-shadow;
        color: $white-color; } }

// END HEADER BLOCK

// CONTENT BLOCK
.content {
    background-color: #fff;
    flex-basis: 91.85%;
    // overflow: hidden
    // overflow-y: scroll
    // max-height: 8.375%
    position: relative; }

// START CONTENT-HEAD BLOCK
.content-head {
    min-height: 8.25%;
    background-color: #f1f1f1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: $content-head-border; }

.dashboard-reload {
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-left: 2rem;

    & svg {
        width: 2.7rem;
        height: 2.7rem;
        fill: $accent;
        margin-right: .7rem; }

    &__btn {
        color: $text-color;
        font-size: 1.1rem;
        border: 1px solid rgba(164, 164, 164, .53);
        border-radius: 1.6rem;
        text-align: center;
        padding: .8rem 1.5rem .8rem 1.5rem;
        outline: none;
        cursor: pointer;
        background-color: $bg-content-color; } }

.users-online {
    display: flex;
    align-items: center;
    margin-right: 2rem;

    &__btn {
        color: $text-color;
        font-size: .9rem;
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.31);
        border: $border-btn;
        border-radius: 1.6rem;
        text-align: center;
        padding: .9rem 1.4rem .8rem 1.4rem;
        background-color: $bg-content-color;
        outline: none;
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        line-height: 1; }

    &__chat-icon {
        width: 1.2rem;
        height: 1.2rem;
        fill: #bbbbbb;
        margin-right: .5rem; }

    &__arrow-icon {
        width: .8rem;
        height: .8rem;
        fill: #a6a6a6;
        margin-left: .5rem; } }

.circle-icon {
    width: 2.8rem;
    height: 2.8rem;
    background-color: $secondary-color;
    border-radius: 50%;
    line-height: 1;
    position: relative;
    margin-right: .5rem;
    cursor: pointer;


    &__svg {
        width: .8rem;
        height: .8rem;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        fill: $white-color; } }
// END CONTENT-HEADER BLOCK

// START TABS BLOCK
.tabs {
    display: flex;
    align-items: center;
    min-height: 6.75%;
    background-color: $bg-content-color; }

.active-projects {
    display: flex;
    align-items: center;
    background-color: $white-color;
    padding-right: 4.8rem;
    padding-top: 1.5rem;
    padding-left: 2rem;
    padding-bottom: 1rem;
    cursor: pointer;


    &__icon {
        width: 2.8rem;
        height: 2.8rem;
        fill: #d5d5d5;
        margin-right: 1.1rem;

        & svg {
            width: 100%;
            height: 100%; } }

    &__text {
        color: $text-color;
        font-size: 1.6rem;
        line-height: 2.5rem;
        // text-shadow: $text-shadow
        position: relative; }

    &__number {
        font-size: 1.1rem;
        font-weight: 700;
        line-height: 2.1rem;
        color: $white-color;
        background-color: #adadad;
        width: 2.1rem;
        height: 2.1rem;
        border-radius: 50%;
        text-align: center;
        position: absolute;
        right: 0;
        top: 0;
        transform: translate(100%, -50%); } }

.messenger {
    display: flex;
    align-items: center;
    background-color: #f8f8f8;
    padding-right: 4.8rem;
    padding-top: 1.5rem;
    padding-left: 2rem;
    padding-bottom: 1rem;
    border-left: 1px solid #d3d7da;
    border-bottom: 1px solid #d3d7da;
    border-right: 1px solid #d3d7da;
    cursor: pointer;


    &__icon {
        width: 2.8rem;
        height: 2.8rem;
        fill: #d5d5d5;
        margin-right: 1.1rem;

        & svg {
            width: 2.8rem;
            height: 2.8rem; } }

    &__text {
        color: $text-color;
        font-size: 1.6rem;
        line-height: 2.5rem;
        text-shadow: 0 1px 1px rgba(255, 255, 255, .31);
        position: relative; }

    &__number {
        font-size: 1.1rem;
        font-weight: 700;
        line-height: 2.1rem;
        color: $white-color;
        background-color: $accent;
        width: 2.1rem;
        height: 2.1rem;
        border-radius: 50%;
        text-align: center;
        position: absolute;
        right: 0;
        top: 0;
        transform: translate(100%, -50%); } }

.search {
    color: #c1c1c1;
    font-size: 1.5rem;
    text-shadow: 0 1px 1px rgba(255, 255, 255, 0.31);
    line-height: 2.5rem;
    height: 100%;
    padding-bottom: 1.1rem;
    padding-top: 1.6rem;
    align-self: flex-end;
    margin-left: auto;
    margin-right: 8.8rem; }
// END TABS BLOCK

// START MAIN PROJECTS
.main {
    display: flex;
    padding-left: 2rem;
    padding-right: 2rem;
    height: 81%; }

.projects-content {
    flex-basis: 70%;
    background-color: $white-color;
    margin-right: 1.2rem;
    border: $projects-content-border;
    border-radius: 4px;
    margin-top: 2.1rem;
    overflow-y: scroll;
    position: relative; }

.projects-content-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: $projects-content-border;
    padding-top: 1.7rem;
    padding-bottom: 1.6rem;
    padding-left: 2rem;
    padding-right: 2rem;

    &__search {
        color: #c1c1c1;
        font-size: 1.5rem;
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.31);
        line-height: 2.5rem;
        height: 100%; }

    &__add {
        cursor: pointer;
        display: flex;
        align-items: center;
        margin-left: 2rem;

        & svg {
            width: 2.7rem;
            height: 2.7rem;
            fill: $accent; } }

    &__filter {
        color: $text-color;
        font-size: 1.1rem;
        border: 1px solid rgba(164, 164, 164, .53);
        border-radius: 16px;
        text-align: center;
        padding: .8rem 1.5rem .8rem 1.5rem;
        outline: none;
        cursor: pointer;
        background-color: #f1f1f1;
        margin-right: .7rem;

        &_desc {
            color: lighten($text-color, 20%); } } }

.projects-content-item {
    display: flex;
    justify-content: space-between;
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-left: 2rem;
    padding-right: 2rem;
    border-bottom: $projects-content-border; }

.percent {
    display: flex;
    align-items: center;

    &__value {
        font-size: 1.6rem;
        background-color: $accent;
        line-height: 1;
        padding: .5rem .4rem .2rem .4rem;
        border-radius: 4px;
        color: $white-color;
        margin-right: .9rem;

        &_smaller {
            font-size: 1.2rem; } }

    &__title {
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.31);
        color: $text-color;
        font-size: 1.5rem;
        line-height: 1.2; }

    &__archive {
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.31);
        color: $text-color;
        font-size: 1.2rem;
        line-height: 1;
        margin-right: .6rem; }

    &__right-arrow {
        width: .8rem;
        // height: .8rem

        & svg {
            width: 100%;
            height: 100%;
            fill: #a6a6a6; } } }

.update {
    display: flex;
    align-items: center;

    &__name {
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.31);
        color: $text-color;
        font-size: .9rem;
        line-height: 1.3rem;
        text-transform: uppercase;
        margin-right: .5rem;
        border: $projects-content-border;
        padding: .5rem .9rem .3rem;
        border-radius: 1.6rem; }

    &__searc-icon {
        width: 2.3rem;
        height: 2.3rem;
        border: $projects-content-border;
        border-radius: 50%;
        position: relative;
        margin-right: .5rem;
        cursor: pointer;

        &:hover {
            background-color: $accent; }

        & svg {
            width: .8rem;
            height: .8rem;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: #a6a6a6; } }

    &__down-arrow {
        width: 2.3rem;
        height: 2.3rem;
        border: $projects-content-border;
        border-radius: 50%;
        position: relative;

        & svg {
            width: .8rem;
            height: .8rem;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: #a6a6a6; } } }

// START SIDEBAR BLOCK
.activity {
    flex-basis: 30%;
    background-color: $white-color;
    border: $projects-content-border;
    border-radius: 4px;
    margin-top: 2.1rem;
    overflow-y: scroll;
    position: relative; }

.activity-item {
    display: flex;
    align-items: center;
    border-bottom: $projects-content-border;
    padding-top: 1.6rem;
    padding-bottom: 1.5rem;
    padding-left: 2rem;

    &__avatar {
        width: 2.7rem;
        height: 2.7rem;
        border-radius: 50%;
        position: relative;
        margin-right: .7rem;

        & img {
            width: 100%;
            height: 100%; }

        &::before {
            content: "";
            width: .5rem;
            height: .5rem;
            background-color: $secondary-color;
            border-radius: 50%;
            position: absolute;
            top: .2rem;
            left: .2rem; } }

    &__text {
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.31);
        font-size: 1.2rem;
        color: $text-color;
        line-height: 1.4; }

    &__add {
        color: #909090; }


    &__time {
        color: #909090; }

    &__archiv {
        text-shadow: 0 1px 1px rgba(255, 255, 255, 0.31);
        color: $text-color;
        font-size: 1.2rem;
        line-height: 2.5rem;
        line-height: 1;
        margin-right: .6rem; }

    &__right-arrow {
        width: .8rem;

        & svg {
            width: 100%;
            height: 100%;
            fill: #a6a6a6; } }
 }    // END SIDEBAR BLOCK
// END MAIN PROJECTS

// END CONTENT BLOCK

//PERFECT-SCROLLBAR USER SETUP
.ps__rail-y {
    opacity: .3;
    right: .4rem; }
//END PERFECT-SCROLLBAR USER SETUP


