//MEDIA QUERIES
//960
@media screen and (max-width: 960px) {
    html {
        font-size: 50%; } }

//1160
@media screen and (max-width: 1160px) {
    html {
        font-size: 62.5%; } }

//1440
@media screen and (min-width: 1440px) {
    html {
        font-size: 70.3125%; } }

//FULL HD
@media screen and (min-width: 1920px) {
    html {
        font-size: 93.75%; } }

